import React from "react"
import {Link} from "gatsby"
import Img from "gatsby-image";
import {useTranslation} from "react-i18next";
import ButtonBack from "../../components/elements/BackButton";


export default function BlogSingle({post}) {
    const {t} = useTranslation();

    let schema = {
        "@context": "https://schema.org/",
        "@type": "BlogPosting",
        "author": {
            "@type": "Organization",
            "name": "Kalmars Group",
            "url": "https://kalmars.lv/",
            "logo": "https://kalmars.lv/images/brand/logo_text_White-full.svg"
        },
        "headline": post.title,
        "image": window.location.href+post.featuredImage.node.localFile.childImageSharp.fixed,
        "publisher": {
            "@type": "Organization",
            "name": "Kalmars Group",
            "logo": "https://kalmars.lv/images/brand/logo_text_White-full.svg"
        },
        "url": window.location.href+"/"
    }
    var script = document.createElement('script')
    script.type = "application/ld+json"
    script.text = JSON.stringify(schema)
    document.querySelector('body').appendChild(script)

    return (
        <div className="main-wrapper min-h-screen pt-24 lg:pt-36 pb-20">
            <div className="md:container mx-auto px-10 md:px-0 lg:mt-18">
                <span className="flex">
                    <Link to="/blogs/" className="flex">
                        <ButtonBack text={t('return')}/>
                    </Link>
                </span>
                <h1 className="text-3xl md:text-5xl md:text-center text-white font-bold mt-4">
                    {post.title}
                </h1>
                <p className="relative mt-16">
                    <Img style={{ float: 'right', aspectRatio: '1/1' }} className="w-full md:w-1/3 mb-8 md:ml-8" fluid={post.featuredImage.node.localFile.childImageSharp.fluid} alt="Blog image"/>
                    <p className="text-white text-justify" dangerouslySetInnerHTML={{__html: post.content}}/>
                </p>
            </div>
        </div>
    )
}
